<template>
  <div>
    <div v-if="fileEvents.length !== 0" class="aboutRoomFileWrapper">
      <div class="aboutRoomFile" v-for="item in fileEvents" :key="item.event_id">
        <i class="fas fa-file-download"></i>
        <a :href="jsonConvertor(item.event.content.body).url"
           :download="jsonConvertor(item.event.content.body).name">
          <span class="fileName">{{ jsonConvertor(item.event.content.body).name }}</span>
          <span class="fileSize">{{ humanReadableSize(jsonConvertor(item.event.content.body).size) }}</span>
        </a>
      </div>
    </div>
    <div v-else class="emptyBlock">
      <span>There are no images in this chat yet</span>
    </div>
  </div>
</template>
<script>
import f from "@/application/functions";

export default {
  props: {
    fileEvents: Array
  },
  methods: {
    jsonConvertor(data) {
      console.log(data, "Datatattt")
      return JSON.parse(data)
    },
    humanReadableSize(data) {
      return f.formatBytes(data)
    },
  }
}
</script>
<style lang="sass" scoped>
.aboutRoomFileWrapper
  .aboutRoomFile
    display: flex
    justify-content: flex-start
    align-items: flex-start
    border-bottom: 1px solid $color-bg-gen
    margin-bottom: $r
    padding: $r 2 * $r

    &:last-child
      border: none

    a
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      margin: .2em 0

    i
      font-size: 2em
      margin-top: .2em
      margin-right: $r

.emptyBlock
  text-align: center
  padding: $r 0

  span
    font-size: 0.9em
</style>