import topheader from '@/components/chat/topheader'
import _ from "underscore";
import f from "@/application/functions";

import contacts from '@/components/contacts/index.vue'
import {PhotoSwipe, PhotoSwipeGallery} from 'v-photoswipe'
import userspic from '@/components/assets/userspic'
import {mapState} from "vuex";
import members from '@/components/chatInfo/info/members/index.vue'
import images from '@/components/chatInfo/info/images/index.vue'
import files from '@/components/chatInfo/info/files/index.vue'

import avatarsList from '@/components/chatInfo/assets/avatarsList/index.vue'
import chatName from '@/components/chats/assets/name.vue'
import chatIcon from '@/components/chats/assets/icon.vue'
import {push} from "underscore/modules/_setup";

export default {
  data: () => {
    return {
      usr: {},
      ind: 0,
      roomMuted: false,
      encryptedEvents: [],
      imageEvents: [],
      pics: false,
      fileEvents: [],
      inputActive: false,
      menuItems: [
        {
          click: "adminMakeModer",
          title: "Make Moderator",
          icon: "fas fa-user-shield",

        },

        {
          click: "adminBan",
          title: "Ban",
          icon: "fas fa-ban",

          upload: {
            multiple: true
          },
        },
        {
          click: "adminKick",
          title: "Kick",
          icon: "fas fa-user-times"
        }
      ],
      menuItemsModer: [
        {
          click: "moderBan",
          title: "Ban",
          icon: "fas fa-ban"
        },
        {
          click: "moderKick",
          title: "Kick",
          icon: "fas fa-user-times"
        }
      ],
      admin: String,
      imagesList: [],
      localRoomName: '',
      humanSize: Number,
      inviteUserOpened: false,
      msList: [],
      isOpen: false,
      actionMemberShow: false,
      options: {
        index: 0,
        arrowEl: false,
        fullscreenEl: false,
        shareEl: false
      },
      roomName: '',
      nameEdit: false,
      accordionList: [
        {title: 'Members'},
        {title: 'Media'},
        {title: 'Files'}
      ],
      isActive: null,
      isActiveAction: null,
      currentUser: '',
      actionUser: {},
      actionUserMenu: false,
      membersCount: Number
    }

  },
  props: {
    chat: {}
  },
  components: {
    images,
    members,
    files,
    topheader,
    avatarsList,

    userspic,
    contacts,
    chatName,
    chatIcon,
    'v-photoswipe': PhotoSwipe,
    'v-photoswipe-gallery': PhotoSwipeGallery
  },
  computed: {
    ...mapState([
      'minimized',
      'active'
    ]),

    events: function () {
      let pushRules = this.core.mtrx.client._pushProcessor.getPushRuleById(this.chat.roomId)
      if (pushRules !== null) {
        this.roomMuted = true
      }
      return this.m_chat.timeline || {}

    },
    customRoomType: function () {
      var type = this.core.mtrx.customRoomType(this.chat.roomId)


      console.log('type', type)
      return type
    },

    users: function () {

      var users = _.filter(_.map(this.chat?.getJoinedMembers() || [], (m) => {

        return this.$f.deep(this, '$store.state.users.' + m.name) || null

      }), (m) => {
        return m && m.id != this.core.user.userinfo?.id
      })

      return users
    },
    m_chat: function () {

      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)
        this.membersCount = m_chat.currentState.getMembers().length
        return m_chat || {}
      }
    },

    galleryImages: function () {
      let imgArr = []
      this.imageEvents.forEach(function (event) {
        if (event.event.content.msgtype === 'm.image') {
          imgArr.push({
            src: event.event.content.url,
            w: event.event.content.info.w,
            h: event.event.content.info.h
          })
        }
      })
      return imgArr
    },
    userPics: function () {
      return this.membersList.length > 4;
    },
    membersList: function () {

      let members = []
      let pNetUsers = this.$store.state.users
      this.currentUser = this.core.mtrx.client.credentials.userId

      _.mapObject(this.m_chat.currentState.members, function (val, key) {
        if (val.membership !== 'leave') {
          _.map(pNetUsers, function (user) {

            if ('@' + user.id + ':matrix.pocketnet.app' === val.userId) {
              return members.push({
                id: val.userId,
                image: user.image,
                name: user.name,
                level: val.powerLevel,
                roomStatus: val.membership
              })
            }
          })
        }

      })

      return members
    },
    memberRole: function () {
      let me = this.currentUser
      let role = ''
      _.map(this.membersList, function (user) {
        if (me === user.id && user.level === 100) {
          return role = 'Admin'
        }
        if (me === user.id && user.level === 50) {
          return role = 'Moderator'
        }
        if (me === user.id && user.level === 0) {
          return role = 'Member'
        }
      })
      return role
    },
    changeRoomName: function () {
      let roomName = this.m_chat.name
      return roomName.replace('_', ' ')
    }

  },
  mounted() {
    // this.changeName()
    this.eventsList()
  },
  methods: {
    banForgetRoom() {
      let banUserId = ''
      let my = this.m_chat.myUserId
      let members = this.m_chat.currentState.getMembers()
      members.forEach(function (member) {
        if (member !== my) {
          banUserId = member.userId
        }
      })
      this.core.mtrx.client.ban(this.chat.roomId, banUserId, 'leave').then(r => {
        console.log(r, "BANNED")
        return r
      })
    },
    muteRoom() {
      var roomId = this.chat.roomId
      var deviceID = this.core.mtrx.client.deviceId
      let self = this
      if (this.roomMuted) {
        self.core.mtrx.client.deletePushRule('global', 'room', roomId)
        console.log("UNMUTE")
        self.roomMuted = false
      } else {
        self.core.mtrx.client.setRoomMutePushRule('global', roomId, 'true')
        console.log("Mute")
        self.roomMuted = true
      }

      console.log(this.core.mtrx.client.getPushRules(), "Push Rules")

    },

    memberMenu(data) {
      this.usr = data.user
      this.ind = data.index
      this.menuItemClickHandler()
    },

    eventsList() {

      if (typeof this.events.filter == 'function') {

        this.imageEvents = this.events.filter(event => event.event.content.msgtype === 'm.image')
        this.fileEvents = this.events.filter(event => event.event.content.msgtype === 'm.file')

      }
    },

    jsonConvertor(data) {
      return JSON.parse(data)
    },

    toggleItem(index) {
      this.isActive = this.isActive === index ? null : index;
      this.actionMemberShow = null
    },

    menuItemClickHandler(item, rowObject) {
      if (item.click === 'adminMakeModer') {
        this.makeAdmin(rowObject.user)
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'adminBan') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'adminKick') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'moderBan') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      if (item.click === 'moderKick') {
        this.$refs.dropdownMenu.filter((e) => {
          e.hidePopup()
        })
      }

      this.actionUser = rowObject
    },
    forgetRoom() {
       this.core.mtrx.client.leave(this.chat.roomId).then(r=>{
           this.core.mtrx.client.forget(this.chat.roomId, true).then(r=>{
              return r
            })
        })
      this.$router.push({path: '/chats'})

    },
    changeName() {
      this.localRoomName = this.m_chat.name
      this.localRoomName.replace('_', ' ')
    },
    saveRoomName() {
      this.nameEdit = false
      this.inputActive = false
      this.roomName.replace(" ", "_")
      this.core.mtrx.client.setRoomName(this.m_chat.roomId, this.roomName)
      // this.core.mtrx.client.setRoomName(this.chat.roomId)

    },
    makeAdmin(user) {
      let event = this.m_chat.currentState.getStateEvents("m.room.power_levels")
      this.core.mtrx.client.setPowerLevel(this.m_chat.roomId, user, 50, event[0]).then(r => {
      })

    },
    showEditRoomName() {
      this.nameEdit = true
      this.roomName = this.m_chat.name
      this.inputActive = true

    },
    editRoomName() {
      console.log(this.roomName, "omg its name changed")
    },

    memberAction(user) {
      this.actionMemberShow = !this.actionMemberShow
      this.actionUser = user
      this.isActiveAction = this.isActiveAction === user.index ? null : user.index;

    },

    kickUser(user) {
      this.core.mtrx.client.kick(this.m_chat.roomId, user).then(this.$nextTick(function () {
      }))
    },
    showPhotoSwipe(index) {
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },
    accordion: function (event) {

      //Раскомментировать, если нужно, чтобы закрывались остальные пункты при открытии текущего
      // 	var matches = document.querySelectorAll('.accordion-item-head');
      //  	for(var i = 0; i < matches.length; i++) {
      //  		matches[i].classList.remove('active');
      //	 }

      event.target.classList.toggle('active');
    },

    hidePhotoSwipe() {
      this.isOpen = false
    },
    focus() {
      return $event.target.select()
    },
    showUserAbout(user) {
      this.$emit('showAbout', user)
    },

    getClick(room) {
      this.$emit('showRoomInfo', room)
    },

    getJoinedMembers() {
      let pNetUsers = this.$store.state.users
      let users = []
      let membersId = []
      let jMembers = []
      let adminId = ''
      let moderatorId = ''
      return this.core.mtrx.client.getJoinedRoomMembers(this.chat.roomId).then(members => {

        _.map(pNetUsers, function (id) {
          users.push(id)
        })
        _.map(members.joined, function (id) {
          membersId.push({id: id.display_name})
        })


        jMembers = f.filterByUserId(membersId, users)
        _.mapObject(this.chat.currentState.members, function (val, key) {
          if (val.powerLevel === 100) {
            adminId = f.getmatrixid(key)
          }
          if (val.powerLevel === 50) {
            moderatorId = f.getmatrixid(key)
          }
        })

        _.map(jMembers, function (member) {
          if (member.id === adminId) {
            member.role = 'owner'
          }
          if (member.id === moderatorId) {
            member.role = 'moderator'
          }
        })
        return jMembers
      }).then(e => {
        return e
      })


    },

    modalInviteUser() {
      this.inviteUserOpened = true
    },

    closeModal() {
      this.inviteUserOpened = false
    },

    closeContactModal(value) {
      this.inviteUserOpened = value
    }
  }
}