import {PhotoSwipe, PhotoSwipeGallery} from "v-photoswipe";

export default {
  data: function () {
    return {
       options: {
        index: 0,
        arrowEl: false,
        fullscreenEl: false,
        shareEl: false
      },
      isOpen: false,
    }
  },
  props: {
    imageEvents: Array
  },
  components: {
    'v-photoswipe': PhotoSwipe,
    'v-photoswipe-gallery': PhotoSwipeGallery
  },
  computed: {
    galleryImages: function () {
      let imgArr = []
      this.imageEvents.forEach(function (event) {
        if (event.event.content.msgtype === 'm.image') {
          imgArr.push({
            src: event.event.content.url,
            w: event.event.content.info.w,
            h: event.event.content.info.h
          })
        }
      })
      return imgArr
    },
  },
  methods: {
    showPhotoSwipe(index) {
      console.log(index, "index index index")
      this.isOpen = true
      this.$set(this.options, 'index', index)
    },
     hidePhotoSwipe() {
      this.isOpen = false
    },
  }
}