<template>

  <router-link :to="link" v-if="link">
    <span>{{ name }}</span>
  </router-link>
  <div v-else>
    <span class="nameline" v-if="this.chat.currentState.members.length > 2 && chat.name !== '#New Room'">
      {{ chat.name }}
    </span>
    <span class="nameline" v-else>
      {{ name }}
    </span>
  </div>

</template>

<style scoped lang="sass">


</style>

<script>


export default {
  name: 'chatName',

  props: {
    chat: Object,
    preview: Boolean
  },

  computed: {
    link: function () {

      if (this.preview) return

      var members = _.filter(this.chat?.currentState.members || {}, (m) => {
        return this.$f.getmatrixid(m.name) != this.core.user.userinfo?.id
      })

      if (members.length == 1) {
        return 'contact?id=' + this.$f.getmatrixid(members[0].name)
      }


    },
    name: function () {

      if (this.chat.name.indexOf("#") == 0) {

        var names = _.map(_.filter(_.map(this.chat?.currentState.members || {}, (m) => {

          return this.$f.deep(this, '$store.state.users.' + this.$f.getmatrixid(m.name)) || null

        }), (m) => {
          return m && m.name && (_.toArray(this.chat.currentState.members).length > 2 || m.id != this.core.user.userinfo?.id)
        }), (m) => {
          return m.name
        })

        if (names.length) return names.join(', ')

        return 'New Chat'

      } else {
        return this.chat.name || ""
      }

    },
  }
}
</script>
