import {mapState} from 'vuex';
import info from "@/components/chatInfo/info/index.vue"
import Api from '../../application/api'
import _ from 'underscore'

export default {
  name: 'chatInfo',

  components: {
    info
  },
  props: {
    chat: {},
    events: {}
  },

  data: function () {
    return {}

  },

  created() {

  },

  mounted() {
    this.$store.commit('blockactive', {value : true, item : 'chatinfo'})
  },

  destroyed() {
    this.$store.commit('blockactive', {value : false, item : 'chatinfo'})
  },

  watch: {
    //$route: 'getdata'
  },

  computed: mapState({
    pocketnet: state => state.pocketnet,
    minimized: state => state.minimized,
    active: state => state.active,
    auth: state => state.auth,
    m_chat: function () {
      if (this.chat && this.chat.roomId) {
        var m_chat = this.core.mtrx.client.getRoom(this.chat.roomId)

        return m_chat || {}
      }
    },
    joinedMembers: function () {
      let self = this
      return this.core.mtrx.client.getJoinedRoomMembers(self.chat.roomId).then(r => {
        return _.mapObject(r.joined, function (key, value) {
          self.joinedRoomMembers.push(value)
        })
      })
    },
    downloadKeys: function (){
      let self = this
      let accessToken = this.core.mtrx.access.accessToken
       return self.core.mtrx.client.downloadKeysForUsers(this.joinedMembers, accessToken).then(r=>{
         return r
       })
    }

  }),
}